import React from "react"
import { PropTypes } from "prop-types"
import { Anchor, Box } from "grommet"
import {
  Codepen,
  Github,
  Linkedin,
  Medium,
  Npm,
  Spotify,
  Twitter,
} from "grommet-icons"

const Social = ({
  parentProps,
  direction,
  label,
  iconMargin,
  codepen,
  github,
  linkedin,
  medium,
  npm,
  spotify,
  twitter,
}) => {
  return (
    <Box direction={direction} {...parentProps}>
      {codepen && (
        <Anchor
          a11yTitle={`Open Codepen`}
          href={`https://www.codepen.io/${codepen}`}
          icon={<Codepen color="#ae63e4" />}
          label={label && "Codepen"}
          margin={iconMargin}
          rel="noopener"
          target="_blank"
        />
      )}

      {github && (
        <Anchor
          a11yTitle={`Open GitHub`}
          href={`https://www.github.com/${github}`}
          icon={<Github color="#6e5494" />}
          label={label && "GitHub"}
          margin={iconMargin}
          rel="noopener"
          target="_blank"
        />
      )}

      {linkedin && (
        <Anchor
          a11yTitle={`Open LinkedIn`}
          href={`https://www.linkedin.com/in/${linkedin}`}
          icon={<Linkedin color="plain" />}
          label={label && "LinkedIn"}
          margin={iconMargin}
          rel="noopener"
          target="_blank"
        />
      )}

      {medium && (
        <Anchor
          a11yTitle={`Open Medium`}
          href={`https://www.medium.com/@${medium}`}
          icon={<Medium color="#00ab6c" />}
          label={label && "Medium"}
          margin={iconMargin}
          rel="noopener"
          target="_blank"
        />
      )}

      {npm && (
        <Anchor
          a11yTitle={`Open npm`}
          href={`https://www.npmjs.com/~${npm}`}
          icon={<Npm color="plain" />}
          label={label && "npm"}
          margin={iconMargin}
          rel="noopener"
          target="_blank"
        />
      )}

      {spotify && (
        <Anchor
          a11yTitle={`Open Spotify`}
          href={`https://open.spotify.com/user/${spotify}`}
          icon={<Spotify color="plain" />}
          label={label && "Spotify"}
          margin={iconMargin}
          rel="noopener"
          target="_blank"
        />
      )}

      {twitter && (
        <Anchor
          a11yTitle={`Open Twitter`}
          href={`https://twitter.com/${twitter}`}
          icon={<Twitter color="plain" />}
          label={label && "Twitter"}
          margin={iconMargin}
          rel="noopener"
          target="_blank"
        />
      )}
    </Box>
  )
}

Social.defaultProps = {
  direction: "row",
  label: false,
  iconMargin: "0",
}

Social.propTypes = {
  parentProps: PropTypes.object,
  direction: PropTypes.string,
  label: PropTypes.bool,
  iconMargin: PropTypes.string,
  codepen: PropTypes.string,
  dribbble: PropTypes.string,
  github: PropTypes.string,
  linkedin: PropTypes.string,
  medium: PropTypes.string,
  npm: PropTypes.string,
  spotify: PropTypes.string,
  twitter: PropTypes.string,
}

export default Social
