import React from "react"
import { globalHistory as history } from "@reach/router"
import { PropTypes } from "prop-types"
import { Link } from "gatsby"
import { Anchor, Box, Image, Heading, List, Paragraph, Text } from "grommet"
import styled from "styled-components"
import useDarkMode from "use-dark-mode"

import Responsive from "components/atoms/responsive"

const Container = styled(Box)`
  transition: background 0.75s !important;
  transition-timing-function: ease-in;
`

const Logo = styled(Image)`
  border-radius: 25px;
  box-shadow: 0 0 36px 0 rgba(238, 238, 238, 0.25);
`

const Label = styled(Text)`
  font-weight: bold;
  margin-bottom: 1rem;
  text-transform: uppercase;
`

const Iframe = styled.iframe`
  margin: 0 auto;
  width: 100%;
  height: 60vh;
  height: calc(var(--vh, 1vh) * 60);
  max-width: 75vw;
  min-height: 480px;
  max-height: 900px;
  border-radius: 6px;
  border-color: transparent;

  body {
    ::-webkit-scrollbar {
      all: unset !important;
    }
  }
`

const Screenshot = styled(Image)`
  width: auto;
  height: auto;
  max-width: 75vw;
  max-height: 90vh;
  max-height: calc(var(--vh, 1vh) * 90);
  border-radius: 6px;
  box-shadow: 0 6px 26px 0 rgba(0, 0, 0, 0.08);
  transition: filter 0.1s;

  &:hover {
    filter: grayscale(0.5);
  }

  @media screen and (max-width: ${(props) =>
      props.theme.global.size["large"]}) {
    max-width: 100%;
    max-height: unset;
    margin: 16px 0;
  }
`

const CustomAnchor = styled(Anchor)`
  padding: 6px 20px;
  color: ${(props) => props.theme.global.colors.text["dark"]};
  background-color: ${(props) => props.theme.global.colors.text["light"]};
  border-radius: 5px;
  font-size: 16px;
  text-transform: uppercase;

  &:hover {
    opacity: 0.6;
    text-decoration: none;
  }
`
const CustomList = styled(List)`
  display: flex;
  flex-wrap: wrap;

  li {
    margin: 0 8px 8px 0;
    padding: 4px 12px;
    color: ${(props) => props.theme.global.colors.text["dark"]};
    background-color: ${(props) => props.theme.global.colors.text["light"]};
    border-radius: 15px;
  }

  @media screen and (min-width: ${(props) =>
      props.theme.global.size["large"]}) {
    width: 300px;
  }
`

const areas = {
  small: [
    { name: "one", start: [0, 0], end: [0, 0] },
    { name: "two", start: [0, 1], end: [0, 1] },
  ],
  medium: [
    { name: "one", start: [0, 0], end: [0, 0] },
    { name: "two", start: [2, 0], end: [2, 0] },
  ],
  large: [
    { name: "one", start: [0, 0], end: [0, 0] },
    { name: "two", start: [2, 0], end: [2, 0] },
  ],
}

const Project = (props) => {
  const {
    location: { pathname },
  } = history
  const isDarkMode = useDarkMode(true).value
  const customBackground = isDarkMode ? "#181818" : "#f5f6fa"
  const customBorder = isDarkMode ? "dark-1" : "light-3"

  return (
    <Container
      align="center"
      background={
        props.background
          ? props.background
          : props.index % 2 === 0
          ? customBackground
          : null
      }
      border={
        props.border && {
          color: customBorder,
          side: "bottom",
        }
      }
      pad={{ horizontal: "large", vertical: "xlarge" }}
      responsive={true}
    >
      <Text
        a11yTitle="Project Year"
        margin={{ bottom: "small" }}
        size="small"
        weight={300}
      >
        {props.year}
      </Text>

      {props.logo && (
        <Logo
          a11yTitle="Project Logo"
          alt="Project Screenshot"
          height="60"
          loading="lazy"
          src={props.logo}
        ></Logo>
      )}

      <Heading
        a11yTitle="Project Title"
        margin={{ top: "small", bottom: "medium" }}
        size="medium"
      >
        {props.title}
      </Heading>

      {props.website &&
        (pathname === "/" && props.case ? (
          <CustomAnchor
            a11yTitle={`Read More`}
            as={Link}
            label="Read More"
            reverse
            to={props.path}
          />
        ) : (
          <CustomAnchor
            a11yTitle={`Visit ${props.title}`}
            href={props.website}
            label="View More"
            rel="noopener"
            reverse
            target="_blank"
          />
        ))}

      {props.iframe ? (
        <Box width="100%" margin={{ vertical: "large" }}>
          <Iframe loading="lazy" src={props.website} title={props.title} />
        </Box>
      ) : (
        props.image && (
          <Box margin={{ vertical: "large" }}>
            <Anchor
              a11yTitle={`Visit ${props.title}`}
              href={props.website}
              rel="noopener"
              target="_blank"
            >
              <Screenshot
                a11yTitle={`Screenshot of ${props.title}`}
                alt={`Screenshot of ${props.title}`}
                fit="cover"
                loading="lazy"
                src={props.image}
              />
            </Anchor>
          </Box>
        )
      )}

      <Responsive gap="medium" areas={areas}>
        <Box gridArea="one">
          <Label>Description</Label>
          <Paragraph a11yTitle="Project Description" margin="0">
            {props.description}
          </Paragraph>
        </Box>

        {props.tags && (
          <Box gridArea="two">
            <Label>Tags</Label>
            <CustomList
              a11yTitle="Project Tags"
              data={props.tags}
              border={{ size: "none" }}
              pad={"0"}
            />
          </Box>
        )}
      </Responsive>
    </Container>
  )
}

Project.propTypes = {
  active: PropTypes.bool,
  background: PropTypes.string,
  border: PropTypes.bool,
  case: PropTypes.bool,
  description: PropTypes.string.isRequired,
  iframe: PropTypes.bool,
  image: PropTypes.string,
  index: PropTypes.number,
  logo: PropTypes.string,
  location: PropTypes.object,
  path: PropTypes.string,
  tags: PropTypes.array,
  title: PropTypes.string.isRequired,
  website: PropTypes.string,
  year: PropTypes.string.isRequired,
}

export default Project
