import React, { useMemo, useState } from "react"
import PropTypes from "prop-types"
import { Grommet, Box, CheckBox } from "grommet"
import { grommet } from "grommet/themes"
import { deepMerge } from "grommet/utils"
import styled from "styled-components"
import useDarkMode from "use-dark-mode"

import { GlobalStyles } from "components/styles"

const StyledGrommet = styled(Grommet)`
  transition: background 0.75s !important;
  transition-timing-function: ease-in;
`

const Toogle = styled(Box)`
  z-index: 999;
  position: fixed;
  top: 1rem;
  right: 1rem;
`

const Layout = ({ children }) => {
  const [themeMode, setThemeMode] = useState("dark")

  const extend = {
    global: {
      breakpoints: {
        small: {
          value: 767,
        },
        medium: {
          value: 1200,
        },
        large: {
          value: 1600,
        },
      },
      colors: {
        brand: "#b794de",
      },
      font: {
        family: "'Roboto', sans-serif",
      },
    },
  }

  const theme = useMemo(() => deepMerge(grommet, extend), [grommet, extend])

  const darkMode = useDarkMode(true, {
    onChange: (value) => {
      setThemeMode(value ? "dark" : "light")
    },
  })

  return (
    <StyledGrommet key={themeMode} theme={theme} themeMode={themeMode}>
      <GlobalStyles />

      <Toogle>
        <CheckBox
          label={darkMode.value ? "" : ""}
          checked={darkMode.value}
          onChange={darkMode.toggle}
          toggle={true}
        />
      </Toogle>

      <main className="preload">{children}</main>
    </StyledGrommet>
  )
}

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Layout
