import React from "react"
import PropTypes from "prop-types"
import { Box } from "grommet"

const Wrapper = ({ children, childrenProps, parentProps }) => {
  return (
    <Box
      align="center"
      justify="center"
      overflow="auto"
      width="100%"
      {...parentProps}
    >
      <Box
        fill={true}
        responsive={true}
        width={{ max: "1200px" }}
        {...childrenProps}
      >
        {children}
      </Box>
    </Box>
  )
}

Wrapper.propTypes = {
  children: PropTypes.node.isRequired,
  childrenProps: PropTypes.object,
  parentProps: PropTypes.object,
}

export default Wrapper
